<template>
  <div class="flowchart">
    <div class="editor-bar">
      <el-button type="info" @click="initData">流程</el-button>
      <el-button type="info" @click="reset">清空</el-button>
    </div>
    <div class="editor-container">
      <div class="main">
        <drop class="drop-workplace" @drop="handleDrop" id="workplace">
          <jsplumbchart
            :data="{stepData:flowData,links:this.links,jsPlumb:jsPlumb}"
            @modifyChart="modifyChart"
            @nodedblClick="nodedblClick"
            ref="jsplumbchart"
          ></jsplumbchart>
          <!-- <jsplumb-chart
            :data="{stepData:flowData,links:this.links}"
            @modifyChart="modifyChart"
            @nodedblClick="nodedblClick"
            ref="jsplumbchart"
          ></jsplumb-chart>-->
        </drop>
      </div>
      <div class="aside">
        <rightaside></rightaside>
      </div>
    </div>

    <el-dialog
      :title="nodeType+'设置'"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      v-dialog-drag
      @open="showDailog"
      width="70%"
      custom-class="custom-class-dialog"
    >
      <component :is="dialogComponent" :nodeType="nodeType"></component>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import _ from "lodash";
import rightaside from "./rightaside/index";
import { modules1 } from "@/service";

import jsplumbchart from "@/components/jsplumbchart/index";

import dataflow from "@/components/flowchart/node/dataflow/index";
import workflow from "@/components/flowchart/node/workflow/index";
import streamflow from "@/components/flowchart/node/streamflow/index";

import { flowData } from "mock/data/flowData.js";

// // import vue-panzoom
import panzoom from "panzoom";
import jsPlumb from "static/lib/jsPlumb/jsPlumb-2.2.3-min";

// import svgPanZoom from "@/utils/svg-pan-zoom/svg-pan-zoom";
export default {
  components: {
    rightaside,
    jsplumbchart
  },
  data() {
    return {
      jsplumbInstance: null,
      flowType: "",
      flowData: {},
      links: [],
      self: this,
      dialogVisible: false,
      dialogComponent: "",
      nodeType: "",
      newflowdata: [],
      jsPlumb: jsPlumb
    };
  },

  //tesst
  created() {},
  mounted() {
    this.initData();
  },
  methods: {
    showDailog() {
      if (this.flowType == "dataflow") {
        this.dialogComponent = dataflow;
      }
      if (this.flowType == "streamflow") {
        this.dialogComponent = streamflow;
      }
      if (this.flowType == "workflow") {
        this.dialogComponent = workflow;
      }
    },
    handleClose(done) {
      done();
      this.dialogComponent = "";
      // this.$confirm("确认关闭？")
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    },
    showStepDialog(val) {
      this.dialogVisible = true;
    },
    initData() {
      let res = _.cloneDeep(flowData);

      this.flowData = res.steps;
      this.flowType = res.flowType;
      this.links = res.links;
    },
    reset() {
      this.flowData = [];
      this.$refs.jsplumbchart.reset();
    },
    handleDrop(val) {
      // console.log("  handleDrop(val) {", val);
      this.flowData.push(this.getCurrentNode(val));
    },
    getCurrentNode(data) {
      return {
        id: data.drawIcon.id + "_" + (this.flowData.length + +1),
        name: data.drawIcon.name,
        type: data.drawIcon.type,
        x: event.offsetX,
        y: event.offsetY,
        stepSettings: data.drawIcon.stepSettings
      };
    },
    modifyChart(val) {
      this.flowData = val.stepData;
      this.links = val.links;
    },
    nodedblClick(val) {}
  }
};
</script>
<style lang="scss" scoped>
.flowchart {
  position: fixed;
  top: 60px;
  left: 200px;
  // right: -200px;
  bottom: 0;
  width: calc(100vw - 200px);
  display: flex;
  flex-direction: column;
  //tsest
  .editor-bar {
    height: 50px;
    padding: 0 50px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .editor-container {
    display: flex;
    width: 100%;
    flex: 1 1 auto;
    .main {
      flex: 1 1 auto;
      background-image: url("../../../../../assets/editor/designBg.png");
      .drop-workplace {
        width: 100%;
        height: 100%;
        position: relative;
      }
    }
    .aside {
      width: 250px;
      z-index: 100;
    }
  }
}
</style>



